export default {
  data () {
    return {
      activeIndex: 0,
      tabList: [
        '全部',
        '待付款',
        '待收货',
        '退款/退货'
      ],
      orderList: []
    }
  },
  onLoad (options) {
    this.activeIndex = parseInt(options.activeIndex)
    this.queryOrderList()
    // 需要重置吗？no
  },
  methods: {
    queryOrderList () {
      this.$request({
        url: `/api/public/v1/my/orders/all?type=${this.activeIndex + 1}`,
        isAuth: true
      }).then(data => {
        console.log(data)
        this.orderList = data.orders
      })
    },
    changeTab (index) {
      // 样式
      this.activeIndex = index
      this.queryOrderList()
    }
  }
}
